<template>
    <main id="register" style="padding-top:30px;">
        <div class="row">
            <div class="col-4"></div>
            <div class="col-4">
                <img src="../assets/img/common/title_register.png" style="height:180px;">
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">姓名</span>
                    </div>
                    <input type="text" class="form-control" placeholder="Name" aria-label="Name"
                        aria-describedby="basic-addon1" v-model="member.name">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">手机号码</span>
                    </div>
                    <input type="tel" class="form-control" placeholder="Tel" aria-label="Tel"
                        aria-describedby="basic-addon1" v-model="member.tel"
                        onkeydown="if(event.keyCode==32){return false;}">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">性别</span>
                    </div>
                    <div class="radio" style="padding-top:7px;padding-left:78px;">
                        <label class="radio-inline">
                            <input type="radio" name="sex" value="男" v-model="member.sex">男
                        </label>
                        <label class="radio-inline">
                            <input type="radio" name="sex" value="女" v-model="member.sex" style="margin-left:15px;">女
                        </label>
                    </div>
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">年龄</span>
                    </div>
                    <input type="number" class="form-control" placeholder="Age" aria-label="Age"
                        aria-describedby="basic-addon1" v-model="member.age"
                        onkeydown="if(event.keyCode==32){return false;}" min="0" max="150">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">密码</span>
                    </div>
                    <input type="password" class="form-control" placeholder="Password" aria-label="Password"
                        aria-describedby="basic-addon1" v-model="member.password"
                        onkeydown="if(event.keyCode==32){return false;}">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">确认密码</span>
                    </div>
                    <input type="password" class="form-control" placeholder="ConfirmPassword"
                        aria-label="ConfirmPassword" aria-describedby="basic-addon1" v-model="confirmPassword"
                        onkeydown="if(event.keyCode==32){return false;}">
                </div>

                <slide-check v-on:doCheck="doCheck"></slide-check>

                <div style="height:40px;">
                    <span class="glyphicon glyphicon-remove" style="color:red;padding-top:10px;"
                        v-if="errorMsg!=''">{{errorMsg}}</span>
                </div>
                <a type="button" class="btn btn-primary" style="width:100%" @click="doRegister">注册</a><br>
                <a type="button" class="btn btn-light" style="width:100%" href="/#/login">返回登录</a>
            </div>

            <div class="col-4"></div>
        </div>
    </main>
</template>

<script>
    import SlideCheck from "../components/SlideCheck.vue";

    export default {
        name: 'register',
        data() {
            return {
                member: {
                    name: '',
                    tel: '',
                    password: '',
                    sex: '男',
                    age: ''
                },
                confirmPassword: '',
                errorMsg: '',
                checkMsg: false
            }
        },
        components: {
            SlideCheck
        },
        methods: {
            doCheck() {
                this.checkMsg = true;
                if (this.errorMsg == "请完成验证") {
                    this.errorMsg = "";
                }
            },
            doRegister() {
                var name = this.member.name;
                var tel = this.member.tel;
                var password = this.member.password;
                var age = this.member.age;
                if (name == "") {
                    this.errorMsg = "请输入姓名";
                } else if (!(/^[1][3,4,5,7,8][0-9]{9}$/.test(tel))) {
                    this.errorMsg = "请输入有效的手机号码";
                } else if (!(/^[0-9]+$/.test(age))) {
                    this.errorMsg = "请输入正确的年龄";
                } else if (password == "") {
                    this.errorMsg = "请输入密码";
                } else if (password.length < 6) {
                    this.errorMsg = "密码长度最短六位";
                } else if (password != this.confirmPassword) {
                    this.errorMsg = "两次输入密码不一致";
                } else if (this.checkMsg == false) {
                    this.errorMsg = "请完成验证";
                } else {
                    var sex = this.member.sex;
                    if (sex == "男") {
                        this.member.sex = 0;
                    } else {
                        this.member.sex = 1;
                    }
                    this.axios({
                        method: "post",
                        url: "/api/user/doRegister.action",
                        data: this.member
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) { //注册成功
                                this.$router.push("/login");
                                this.$emit("showAlert", true, "注册成功");
                                this.errorMsg = "";
                            } else {
                                this.errorMsg = map.data;
                                if (this.member.sex == 0) {
                                    this.member.sex = "男";
                                } else {
                                    this.member.sex = "女";
                                }
                            }
                        }
                    }).catch((error) => {
                        this.$emit("showAlert", true, "服务器未响应,请稍后重试");
                    });
                }
            }
        }
    }
</script>

<style scoped>
    #title {
        margin-top: 25px;
    }
</style>