//公共函数工具类
//在main.js中引入即可使用变量与函数
// import baseUtils from './assets/js/BaseUtils.js';
// Vue.use(baseUtils);
exports.install = function (Vue, options) {
    Vue.prototype.getYachtImgUrl = function (img) { //获取游艇图片路径函数,方便维护图片路径
        if (img != null && img != "") {
            return require("D:/yachtImg/" + img);
        }
    };
    Vue.prototype.getHomeImgUrl = function (img) { //获取首页图片路径函数,方便维护图片路径
        if (img != null && img != "") {
            return require("D:/yachtImg/homeImg/" + img);
        }
    };
    //订单word文件下载url
    Vue.prototype.downLoadOrder = "http://localhost:8760/api/file/downloadWord.action?orderId=";
    //前往支付url
    Vue.prototype.toPay = "http://localhost:8760/api/pay/member/toPay.action";
    //home页图片存放路径
    Vue.prototype.homeImgPrefix = "D:/yachtImg/homeImg/";
};