/*
路由器对象模块
*/

import Vue from 'vue';
import VueRouter from 'vue-router';

import Login from '../views/Login.vue';
import Register from '../views/Register.vue';
import YachtHire from '../views/YachtHire.vue';
import YachtSale from '../views/YachtSale.vue';
import YachtSubscribe from '../views/YachtSubscribe.vue';
import AdminLogin from '../views/AdminLogin.vue';
import User from '../views/User.vue';
import Home from '../views/Home.vue';

//声明使用插件
Vue.use(VueRouter);

let router = new VueRouter({
    routes: [{
            path: '/login',
            name: 'login',
            component: Login
        }, {
            path: '/register',
            name: 'register',
            component: Register
        }, {
            path: '/yachtHire',
            name: 'yachtHire',
            component: YachtHire
        },  {
            path: '/yachtSale',
            name: 'yachtSale',
            component: YachtSale
        }, {
            path: '/yachtSubscribe',
            name: 'yachtSubscribe',
            component: YachtSubscribe
        }, {
            path: '/admin',
            name: 'admin',
            component: AdminLogin
        },{
            path: '/user',
            name: 'user',
            component: User
        },{
            path: '/home',
            name: 'home',
            component: Home
        },
        {
            path: '/',
            redirect: '/home'
        }
    ]
});

export default router