<template>
    <div id="yachtOrder">
        <main class="col">
            <div class="col-12 div_middle">
                <h1 id="title">用户信息</h1>
            </div>
            <div class="col-12 div_middle">
                <table class="table table-hover" style="font-size:20px;text-align:left;">
                    <tr>
                        <td style="width:500px;">
                            <b>姓名:</b>
                            <span v-if="!ifUpdate"> {{member.name}}</span>
                            <input v-if="ifUpdate" class="form-control col-8" type="text" v-model="user.name">
                        </td>
                        <td><b>性别:</b>
                            <span v-if="!ifUpdate"> {{member.sex == 1?'女':'男'}}</span>
                            <div v-if="ifUpdate">
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="男" v-model="user.sex">男
                                </label>
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="女" v-model="user.sex"
                                        style="margin-left:15px;">女
                                </label>
                            </div>
                        </td>
                        <td></td>
                    </tr>
                    <tr>
                        <td><b>年龄:</b>
                            <span v-if="!ifUpdate"> {{member.age}}</span>
                            <input v-if="ifUpdate" class="form-control col-8" type="number" max="150" min="0"
                                v-model="user.age" onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td><b>手机号码:</b>
                            <span v-if="!ifUpdate"> {{member.tel}}</span>
                            <input v-if="ifUpdate" class="form-control" type="tel" v-model="user.tel"
                                disabled="disabled">
                        </td>
                        <td style="text-align:right;">
                            <button class="btn btn-sm btn-info" @click="changeToUpdate" v-if="!ifUpdate">修改信息</button>
                            <button class="btn btn-sm btn-success" v-if="ifUpdate"
                                @click="updateMemberMsg">保存修改</button>
                            <button class="btn btn-sm btn-danger" data-toggle="modal"
                                data-target="#updatePwdModal">修改密码</button>
                        </td>
                    </tr>
                </table>
            </div>
            <hr>
            <div class="col-12 div_middle">
                <h1 id="title">我的订单</h1>
            </div>
            <div class="col-12" style="height:610px;">
                <table class="table"
                    style="font-size:13px;white-space:nowrap; text-align:center;vertical-align:middle; ">
                    <thead>
                        <tr>
                            <td>订单号 (可导出订单)</td>
                            <td>联系电话</td>
                            <td>联系人</td>
                            <td>游艇信息</td>
                            <td>使用时间</td>
                            <td>产品类型</td>
                            <td>订单状态</td>
                            <td>总金额</td>
                            <td>下单时间</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="consumeList.length<=0">
                            <td colspan="9">
                                <h1 style="color:lightgrey;">暂无订单项</h1>
                            </td>
                        </tr>
                        <tr v-for="consume in consumeList" :key="consume.orderId">
                            <td><a :href="downLoadOrder+consume.orderId" title="导出订单文件">{{consume.orderId}}</a></td>
                            <td>{{consume.tel}}</td>
                            <td>{{consume.name}}</td>
                            <td>{{consume.yacht.name}}<a style="cursor:pointer;color:blue;" type="button"
                                    data-toggle="modal" data-target="#yachtDetailModal"
                                    @click="getYachtDetail(consume.yachtId)"> 详情</a></td>
                            <td>
                                <span v-if="consume.yacht.state==1">{{consume.useTime|formatDate}}</span>
                                <span v-if="consume.yacht.state==2||consume.yacht.state==3">无</span>
                            </td>
                            <td>
                                <span v-if="consume.yacht.state==1">租赁</span>
                                <span v-if="consume.yacht.state==2||consume.yacht.state==3">购买</span>
                            </td>
                            <td>
                                <span v-if="consume.state==1" style="color:green">已支付</span>
                                <span v-if="consume.state==0" style="color:red">
                                    <a class="btn btn-sm btn-success" v-if="consume.state==0" :href="toPay">支付</a>
                                </span>
                                <span v-if="consume.state==2" style="color:lightgrey">订单无效</span>
                            </td>
                            <td style="color:orange;font-size:15px;"><b>￥{{consume.price/100|NumFormat}}</b></td>
                            <td>{{consume.created|formatDate}}</td>
                            <td>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </main>

        <div id="yachtDetailModal" class="modal fade bs-example-modal-sm">
            <div class="modal-dialog modal-sm modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-body">
                        <p style="margin-top:13px;">
                            <b>游艇编号:</b> {{yacht.id}}<br>
                            <b>游艇名称:</b> {{yacht.name}}<br>
                            <b>游艇颜色:</b> {{yacht.color}}<br>
                            <b>游艇长度:</b> {{yacht.length}}米<br>
                            <b>限载人数:</b> {{yacht.members}}人<br>
                            <b>停靠地址:</b> {{yacht.address}}<br>
                            <span v-if="yachtScheme"><b>包艇时长:</b> {{yachtScheme.time}}小时</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <!-- 修改密码模态框 begin -->
        <div id="updatePwdModal" class="modal fade bs-example-modal-sm">
            <div class="modal-dialog modal-sm">
                <div class="modal-content">
                    <!-- 模态框头部 -->
                    <div class="modal-header">
                        <h4 class="modal-title">修改密码</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <!-- 模态框主体 -->
                    <div class="modal-body">
                        <div class="form-group">
                            <label>原密码:</label>
                            <input type="password" class="form-control" v-model="oldPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>新密码:</label>
                            <input type="password" class="form-control" v-model="newPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>重复新密码:</label>
                            <input type="password" class="form-control" v-model="confirmPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                    </div>
                    <!-- 模态框底部 -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" @click="updatePassword">确定修改</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 修改密码模态框 end -->
    </div>
</template>

<script>
    export default {
        name: 'yachtOrder',
        data() {
            return {
                user: {
                    id: " ",
                    tel: "",
                    name: "",
                    sex: "",
                    age: "",
                    level: ""
                },
                consume: {
                    orderId: "",
                    tel: "",
                    name: "",
                    yachtId: "",
                    schemeId: "",
                    useTime: "",
                    state: "",
                    yacht: "",
                    created: "",
                    yachtScheme: ""
                },
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    state: "",
                    price: "",
                    yachtImgList: []
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                yachtSchemeList: [],
                selectScheme: "",
                selectUseTime: "",
                consumeList: [],
                ifUpdate: false,
                oldPassword: "",
                newPassword: "",
                confirmPassword: ""
            }
        },
        props: ['member'],
        methods: {
            getMemberConsumes() {
                this.axios({
                    url: "/api/consume/member/getMemberConsumes.action",
                    method: "get"
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //未登录
                        this.$emit("clearUser");
                        this.$router.push("/#/login");
                    } else if (map.code == 200) {
                        this.consumeList = JSON.parse(map.data);
                        this.consumeList = this.consumeList.reverse();
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //无权限
                        window.location.href = '/#/login';
                        this.$emit("clearUser");
                    }
                });
            },
            getYachtDetail(yachtId) {
                var consume = this.consumeList.find(function (consume) {
                    return consume.yachtId === parseInt(yachtId);
                });
                this.yacht = consume.yacht;
                this.yachtScheme = consume.yachtScheme;
            },
            changeToUpdate() {
                this.user.id = this.member.id;
                this.user.name = this.member.name;
                this.user.age = this.member.age;
                this.user.sex = this.member.sex;
                this.user.tel = this.member.tel;
                if (this.user.sex == 0) {
                    this.user.sex = "男";
                } else if (this.user.sex == 1) {
                    this.user.sex = "女";
                }
                this.ifUpdate = true;
            },
            updateMemberMsg() { //修改用户基础信息
                if (!(/^[0-9]+$/.test(this.user.age))) {
                    this.$emit("showAlert", true, "请输入正确的年龄");
                } else {
                    if (this.user.sex == "男") {
                        this.user.sex = 0;
                    } else {
                        this.user.sex = 1;
                    }
                    this.axios({
                        url: "/api/member/updateMemberMsg.action",
                        method: "post",
                        data: this.user
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) {
                                this.$emit("showAlert", true, "修改成功");
                                this.$emit("getUser");
                                this.ifUpdate = false;
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        this.$emit("showAlert", true, "服务器异常,请稍后重试");
                    });
                }
            },
            updatePassword() { //修改密码
                if (this.newPassword.length < 6) {
                    this.$emit("showAlert", true, "密码长度最短为六位");
                } else if (this.confirmPassword != this.newPassword) {
                    this.$emit("showAlert", true, "两次输入的密码不一致");
                } else {
                    this.axios({
                        url: "/api/member/member/updatePassword.action",
                        method: "post",
                        data: {
                            id: this.member.id,
                            oldPassword: this.oldPassword,
                            newPassword: this.newPassword
                        },
                        transformRequest: [function (data) { //传递表单信息的post请求
                            // Do whatever you want to transform the data
                            let ret = '';
                            for (let it in data) {
                                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) +
                                    '&';
                            }
                            return ret;
                        }],
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) { //未登录
                            this.$router.push("/login");
                        } else if (map.code == 200) {
                            if (map.data == null) { //修改成功
                                this.clearPassword();
                                $("#updatePwdModal").modal("hide");
                                this.$emit("showAlert", true, "修改成功");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            clearPassword() {
                this.oldPassword = "";
                this.newPassword = "";
                this.confirmPassword = "";
            }
        },
        created() {
            this.getMemberConsumes();
            $(".modal-backdrop").remove();
        },
        filters: {
            formatDate(dataStr) {
                var time = new Date(dataStr);

                function timeAdd0(str) {
                    if (str < 10) {
                        str = '0' + str;
                    }
                    return str
                }
                var y = time.getFullYear();
                var m = time.getMonth() + 1;
                var d = time.getDate();
                var h = time.getHours();
                var mm = time.getMinutes();
                var s = time.getSeconds();
                return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm);
            },
            NumFormat(value) {
                if (!value) return '0.00';

                /*原来用的是Number(value).toFixed(0)，这样取整时有问题，例如0.51取整之后为1，感谢Nils指正*/
                var intPart = Number(value) | 0; //获取整数部分
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //将整数部分逢三一断

                var floatPart = ".00"; //预定义小数部分
                value = value.toString();
                var value2Array = value.split(".");

                //=2表示数据有小数位
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //拿到小数部分

                    if (floatPart.length == 1) { //补0,实际上用不着
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }

            }
        }
    }
</script>

<style scoped>
    #title {
        color: deepskyblue;
        margin-top: 15px;
    }

    #yachtOrder {
        overflow-x: auto
    }
</style>