<template>
    <main id="yachtHire" ondragstart="return false;">
        <div class="col-12 div_middle" style="background-color:#A3E5F5;">
            <img src="../assets/img/common/title_hire.png" style="height:200px;margin-top:10px;margin-bottom:10px;">
        </div>
        <div class="row">
            <div class="yacht" @mouseenter="getDetail(yacht.id);" @mouseleave="getDetail(yacht.id);"
                v-for="yacht in yachtList" :key="yacht.id">
                <button :id="'yachtDetail_'+yacht.id" class="yachtDetail" @click="$router.push({
                        name:'yachtSubscribe',
                        params:{
                            yachtId: yacht.id
                        }
                        })">
                    <span id="yachtText">
                        {{yacht.name}} - 颜色:{{yacht.color}}<br>
                    </span>
                </button>
                <img id="yachtImg" :src="base64ToImgUrl(yacht.yachtImgList[0].img)" style="width:330px;height:189.2px;">
                <div id="yachtMsg">
                    限载{{yacht.members}}人/{{yacht.color}}/{{yacht.length}}米<br>
                    地址:{{yacht.address}}
                </div>
            </div>
            <div class="col-12 div_middle" style="padding-bottom:20px;padding-top:20px;">
                <hr>
                共 {{yachtList.length}} 条记录
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'yachtHire',
        data() {
            return {
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    yachtImgList: []
                },
                yachtImg: {
                    id: "",
                    yachtId: "",
                    img: ""
                },
                yachtList: []
            }
        },
        methods: {
            getDetail(id) {
                var display = $("#yachtDetail_" + id).css("display");
                if (display == "none") {
                    $("#yachtDetail_" + id).css("display", "inline");
                } else {
                    $("#yachtDetail_" + id).css("display", "none");
                }
            },
            getYachtAndMainPic() {
                this.axios({
                    method: "get",
                    url: "/api/yacht/getYachtsAndMainPicByHire.action"
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 200) {
                        var data = JSON.parse(map.data);
                        if (data != null) {
                            this.yachtList = data;
                        } else {
                            this.$emit("showAlert", true, map.data);
                        }
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            base64ToImgUrl(base64) {
                //传入base64字符串,获取对应图片url地址(仅限客户端访问)
                //base64字符串格式:  data:image/png;base64,iVBORw0KGgoAAAANSUhEUgA...
                var arr = base64.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                var blob = new Blob([u8arr], {
                    type: mime
                });
                var imageUrl = (window.URL || window.webkitURL).createObjectURL(blob);
                return imageUrl;
            }
        },
        mounted() {
            this.getYachtAndMainPic();
            this.$emit("changeHireOrSale","");
        }
    }
</script>

<style scoped>
    .yacht {
        width: 332px;
        height:189.2px;
        border: solid 1px black;
        margin-left: 50px;
        margin-top: 35px;
        margin-bottom: 45px;
        position: relative;
    }

    #yachtImg {
        width: 100%;
    }

    #yachtMsg {
        background-color: white;
        border: solid 1px black;
    }

    #title {
        color: deepskyblue;
        margin-top: 35px;
    }

    .yachtDetail {
        z-index: 99px;
        position: absolute;
        width: 330px;
        height:189.2px;
        background-color: lightskyblue;
        border: solid 1px black;
        opacity: 0.8;
        text-align: center;
        display: none;
    }

    #yachtText {
        color: white;
        font-weight: bold;
        font-size: 20px;
        text-shadow: #000 1px 0 0, #000 0 1px 0, #000 -1px 0 0, #000 0 -1px 0;
    }
</style>