<template>
    <main id="admin">
        <div class="row">
            <div class="col-4"></div>
            <div class="col-4" style="padding-top:230px;">
                <h1>DockTease后台系统</h1>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">管理员账号</span>
                    </div>
                    <input type="text" class="form-control" placeholder="Username" aria-label="Username"
                        aria-describedby="basic-addon1" v-model="member.tel">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">密码</span>
                    </div>
                    <input type="password" class="form-control" placeholder="Password" aria-label="Password"
                        aria-describedby="basic-addon1" v-model="member.password">
                </div>

                <a type="button" class="btn btn-primary" style="width:100%" @click="doLogin">登录</a><br>
            </div>

            <div class="col-4"></div>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'admin',
        data() {
            return {
                member: {
                    tel: '',
                    password: ''
                }
            }
        },
        methods: {
            doLogin() { //管理员登录
                var tel = this.member.tel;
                var password = this.member.password;
                if (tel == "") {
                    this.$emit("showAlert", true, "请输入账号");
                } else if (password == "") {
                    this.$emit("showAlert", true, "请输入密码");
                } else {
                    this.axios({
                        method: "post",
                        url: "/api/user/adminLogin.action",
                        data: this.member
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) { //登录成功
                                this.$emit("getUser");
                                window.location.href = '/admin.html/';

                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        this.$emit("showAlert", true, "服务器未响应,请稍后重试");
                    });
                }
            }
        }
    }
</script>

