<template>
    <div id="yachtSubscribe">
        <main ondragstart="return false;">
            <div style="margin-top:15px;float:left;" class="col-6">
                <img :src="base64ToImgUrl(yacht.yachtImgList[0].img)"
                    style="height:430px;width:750px;margin-bottom:35px;">
                <h3>基础信息</h3>
                <table class="table" style="text-align:left">
                    <tr>
                        <td>名称: {{yacht.name}}</td>
                        <td>限载: {{yacht.members}}人</td>
                    </tr>
                    <tr>
                        <td>颜色: {{yacht.color}}</td>
                        <td>艇长: {{yacht.length}}米</td>
                    </tr>
                    <tr>
                        <td colspan="2">地址: {{yacht.address}}</td>
                    </tr>
                </table>
            </div>
            <div style="float:left;" class="col-6">
                <div id="demo" class="carousel slide" data-ride="carousel"
                    style="width:750px;height:430px;margin-bottom:15px;margin-top:15px;padding-right:10px;">
                    <!-- 指示符 -->
                    <!-- <ul class="carousel-indicators">
                        <li data-target="#demo" data-slide-to="0" class="active"></li>
                        <li data-target="#demo" data-slide-to="1"></li>
                        <li data-target="#demo" data-slide-to="2"></li>
                    </ul> -->

                    <!-- 轮播图片 -->
                    <div class="carousel-inner">
                        <div class="carousel-item" v-for="(yachtImg,index) in yacht.yachtImgList.slice(1)"
                            :key="yachtImg.id" :class="{active: index == 0}">
                            <img :src="base64ToImgUrl(yachtImg.img)" style="width:750px;height:430px;">
                        </div>
                    </div>

                    <!-- 左右切换按钮 -->
                    <a class="carousel-control-prev" href="#demo" data-slide="prev">
                        <span class="carousel-control-prev-icon"></span>
                    </a>
                    <a class="carousel-control-next" href="#demo" data-slide="next">
                        <span class="carousel-control-next-icon"></span>
                    </a>

                </div>
                <table class="table" style="margin-top:0px;">
                    <tr>
                        <td>游艇编号</td>
                        <td>{{yacht.id}}</td>
                    </tr>
                    <tr>
                        <td>游艇颜色</td>
                        <td>{{yacht.color}}</td>
                    </tr>
                    <tr v-if="yacht.state == 1">
                        <td>选择套餐</td>
                        <td>
                            <select class="form-control" v-model="selectScheme" @change="getScheme">
                                <option v-for="yachtScheme in yachtSchemeList" :key="yachtScheme.id">
                                    {{yachtScheme.time}}小时
                                </option>
                            </select>
                        </td>
                    </tr>
                    <tr v-if="yacht.state == 1">
                        <td>使用时间</td>
                        <td>
                            <el-date-picker v-model="selectUseTime" type="datetime" placeholder="选择日期时间"
                                @change="getUseTime" format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm">
                            </el-date-picker>
                            <div style="color:red;" v-if="ifUseTimeCan != ''">
                                {{ifUseTimeCan}}
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td></td>

                        <!-- 租赁 -->
                        <td v-if="yacht.state == 1">
                            <div>
                                <span
                                    style="font-size: 20px;color:orange">¥{{yachtScheme.price/100|NumFormat}}</span>／{{yachtScheme.time}}小时／包艇
                            </div>
                        </td>
                        <!-- 购买 -->
                        <td v-if="yacht.state == 2">
                            <div>
                                <span style="font-size: 20px;color:orange">¥{{yacht.price/100|NumFormat}}</span>
                            </div>
                        </td>
                    </tr>
                </table>
                <hr>
                <div style="width:530px;margin-left:150px;margin-bottom:20px;" class="row">
                    <!-- 可以预定 -->
                    <a href="#" class="btn btn-warning" style="width:100%;" data-toggle="modal" data-target="#myModal"
                        v-if="member.id!=''&&selectScheme!=''&&consume.useTime!=''&&ifUseTimeCan==''&&yacht.state==1">立即预订</a>
                    <!-- 没有登录,不可预定 -->
                    <a href="#" class="btn btn-warning" style="width:100%"
                        @click.prevent="$emit('showAlert',true,'请先登录')" v-if="member.id==''&&yacht.state==1">立即预订</a>
                    <!-- 没有选择时间和套餐,不可预定 -->
                    <a href="#" class="btn btn-warning" style="width:100%"
                        @click.prevent="$emit('showAlert',true,'请选择包艇套餐和时间');"
                        v-if="(selectScheme==''||consume.useTime=='')&&member.id!=''&&yacht.state==1">立即预订</a>
                    <!-- 时间点不可选,不可预定 -->
                    <a href="#" class="btn btn-warning" style="width:100%"
                        @click.prevent="$emit('showAlert',true,'该时间点无法预订,请尝试更换其他时间点');"
                        v-if="selectScheme!=''&&consume.useTime!=''&&member.id!=''&&ifUseTimeCan!=''&&yacht.state==1">立即预订</a>
                    <!-- 购买 -->
                    <a href="#" data-toggle="modal" data-target="#myModal" class="btn btn-warning" style="width:100%"
                        v-if="member.id!=''&&yacht.state==2">立即购买</a>
                    <!-- 购买 未登录 -->
                    <a href="#" class="btn btn-warning" style="width:100%"
                        @click.prevent="$emit('showAlert',true,'请先登录')" v-if="member.id==''&&yacht.state==2">立即购买</a>
                </div>
            </div>
        </main>

        <!-- 模态框 -->
        <div class="modal fade" id="myModal">
            <div class="modal-dialog">
                <div class="modal-content">
                    <!-- 模态框头部 -->
                    <div class="modal-header">
                        <h4 class="modal-title">确认订单</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- 模态框主体 -->
                    <div class="modal-body">
                        <div>
                            <h2>【{{yacht.name}}】/{{yacht.color}}</h2>
                            <h3 v-if="yacht.state == 1" style="margin-left:15px;">{{yachtScheme.time}}小时/
                                {{yacht.address}}</h3><br>
                            <h5 v-if="yacht.state == 1" style="margin-left:15px;">发艇时间: {{consume.useTime}}</h5>
                            <hr>
                            <div class="input-group mb-3">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1">联系电话</span>
                                </div>
                                <input type="tel" class="form-control" placeholder="Tel" aria-label="Tel"
                                    aria-describedby="basic-addon1" v-model="consume.tel"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </div>
                            <div class="input-group mb-3">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1">联系人</span>
                                </div>
                                <input type="text" class="form-control" placeholder="Name" aria-label="Name"
                                    aria-describedby="basic-addon1" v-model="consume.name">
                            </div>
                            <hr>
                            <h5>* 请保持联系电话畅通,以便我们准确传达最新通知</h5>
                        </div>
                    </div>

                    <!-- 模态框底部 -->
                    <div class="modal-footer">
                        <span style="margin-right:90px;">
                            <span>应付总额</span>
                            <!-- 租赁 -->
                            <span v-if="yacht.state == 1"
                                style="font-size: 26px;color:orange;">¥{{yachtScheme.price/100}}</span>
                            <!-- 购买 -->
                            <span v-if="yacht.state == 2"
                                style="font-size: 26px;color:orange;">¥{{yacht.price/100|NumFormat}}</span>
                        </span>
                        <button type="button" class="btn btn-warning" @click="addOrder">提交订单</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'yachtSubscribe',
        data() {
            return {
                consume: {
                    id: "",
                    tel: this.member.tel,
                    name: this.member.name,
                    yachtId: "",
                    schemeId: "",
                    useTime: ""
                },
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    price: "",
                    state: "",
                    yachtImgList: [0]
                },
                yachtImg: {
                    id: "",
                    yachtId: "",
                    img: ""
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                yachtSchemeList: [],
                selectScheme: "",
                selectUseTime: "",
                ifUseTimeCan: ""
            }
        },
        props: ['member'],
        methods: {
            getYachtMessage() { //路由传入一个参数:游艇id
                var yachtId = this.$route.params.yachtId;
                if (yachtId == null || yachtId == "") {
                    this.$router.push("/yachtHire");
                } else {
                    this.axios({
                        method: "get",
                        url: "/api/yacht/getYachtMessageById.action",
                        params: {
                            "id": yachtId
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            var data = JSON.parse(map.data);
                            if (data != null) {
                                var yachtJson = data[0];
                                var yachtSchemeListJson = data[1];
                                this.yacht = JSON.parse(yachtJson);
                                this.yachtSchemeList = JSON.parse(yachtSchemeListJson);

                                if (this.yacht.state == 1) {
                                    this.$emit("changeHireOrSale", "hire");
                                } else if (this.yacht.state == 2) {
                                    this.$emit("changeHireOrSale", "sale");
                                } else {
                                    this.$emit("changeHireOrSale", "");
                                }
                            }
                        } else {
                            console.log(map);
                        }
                    }).catch((error) => {
                        this.$router.push("/yachtHire");
                        console.log(error);
                    });
                }
            },
            addOrder() {
                if (this.consume.tel == "") {
                    this.$emit("showAlert", true, "请输入手机号码");
                } else if (!(/^0?1[3|4|5|6|7|8][0-9]\d{8}$/.test(this.consume.tel))) {
                    this.$emit("showAlert", true, "请输入正确的手机号码");
                } else if (this.consume.name == "") {
                    this.$emit("showAlert", true, "请填写联系人");
                } else {
                    this.consume.yachtId = this.yacht.id;
                    if (this.yacht.state == 1) {
                        this.consume.useTime = this.dateStrToStamp(this.consume.useTime);
                        this.consume.schemeId = this.yachtScheme.id;
                        this.consume.price = this.yachtScheme.price;
                    } else if (this.yacht.state == 2) {
                        this.consume.useTime = null;
                        this.consume.schemeId = null;
                        this.consume.price = this.yacht.price;
                    }
                    this.axios({
                        method: "post",
                        url: "/api/consume/member/createConsume.action",
                        data: this.consume
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) { //未登录
                            this.$emit("clearUser");
                            this.$router.push("/login");
                        } else if (map.code == 400) {
                            this.$emit("showAlert", true, "数据传输错误");
                        } else if (map.code == 200) {
                            if (map.data == null) { //生成订单成功
                                this.$router.push("/user");
                                this.$emit("showAlert", true, "订单生成成功,请在五分钟内完成支付");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //没有会员权限
                            this.$emit("showAlert", true, "请先以会员的身份登录");
                        }
                    });
                }
            },
            getScheme() {
                var _this = this;
                var yachtScheme = this.yachtSchemeList.find(function (yachtScheme) {
                    return yachtScheme.time === parseInt(_this.selectScheme);
                });
                this.yachtScheme = yachtScheme;
            },
            getUseTime(val) {
                this.consume.useTime = val;
                if (val != "" && val != null) {
                    this.axios({
                        method: "get",
                        url: "/api/consume/ifTimeCanSelect.action",
                        params: {
                            "useTime": val,
                            "yachtId": this.yacht.id
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) { //可选
                                this.ifUseTimeCan = "";
                            } else {
                                this.ifUseTimeCan = map.data;
                            }
                        } else {
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                    });
                } else {
                    this.ifUseTimeCan = "";
                    this.consume.useTime = "";
                }
            },
            dateStrToStamp(dateStr) {
                var dateTmp = dateStr.replace(/-/g, '/');
                var timestamp = Date.parse(dateStr);
                return timestamp;
            },
            base64ToImgUrl(base64) {
                //传入base64字符串,获取对应图片url地址(仅限客户端访问)
                //base64字符串格式:  data:image/png;base64,iVBORw0KGgoAAAANSUhEUgA...
                var arr = base64.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                var blob = new Blob([u8arr], {
                    type: mime
                });
                var imageUrl = (window.URL || window.webkitURL).createObjectURL(blob);
                return imageUrl;
            }
        },
        mounted() {
            this.getYachtMessage();
        },
        filters: {
            NumFormat(value) {
                if (!value) return '0.00';

                /*原来用的是Number(value).toFixed(0)，这样取整时有问题，例如0.51取整之后为1，感谢Nils指正*/
                var intPart = Number(value) | 0; //获取整数部分
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //将整数部分逢三一断

                var floatPart = ".00"; //预定义小数部分
                value = value.toString();
                var value2Array = value.split(".");

                //=2表示数据有小数位
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //拿到小数部分

                    if (floatPart.length == 1) { //补0,实际上用不着
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }

            }
        }
    }
</script>

<style scope>
    #yachtSubscribe {
        height: 710px;
    }
</style>