    }

    body {
        background: #1f1f1f;
        font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    }

    .cont {
        position: relative;
        overflow: hidden;
        height: 100vh;
        padding: 80px 70px;
    }

    .cont__inner {
        position: relative;
        height: 100%;
    }

    .cont__inner:hover .el__bg:after {
        opacity: 1;
    }

    .el {
        position: absolute;
        left: 0;
        top: 0;
        width: 19.2%;
        height: 100%;
        background: #252525;
        -webkit-transition: width 0.7s, opacity 0.6s 0.7s, z-index 0s 1.3s, -webkit-transform 0.6s 0.7s;
        transition: width 0.7s, opacity 0.6s 0.7s, z-index 0s 1.3s, -webkit-transform 0.6s 0.7s;
        transition: transform 0.6s 0.7s, width 0.7s, opacity 0.6s 0.7s, z-index 0s 1.3s;
        transition: transform 0.6s 0.7s, width 0.7s, opacity 0.6s 0.7s, z-index 0s 1.3s, -webkit-transform 0.6s 0.7s;
    }

    .el:not(.s--active) {
        cursor: pointer;
    }

    .el__overflow {
        overflow: hidden;
        position: relative;
        height: 100%;
    }

    .el__inner {
        overflow: hidden;
        position: relative;
        height: 100%;
        -webkit-transition: -webkit-transform 1s;
        transition: -webkit-transform 1s;
        transition: transform 1s;
        transition: transform 1s, -webkit-transform 1s;
    }

    .cont.s--inactive .el__inner {
        -webkit-transform: translate3d(0, 100%, 0);
        transform: translate3d(0, 100%, 0);
    }

    .el__bg {
        position: relative;
        width: calc(100vw - 140px);
        height: 100%;
        -webkit-transition: -webkit-transform 0.6s 0.7s;
        transition: -webkit-transform 0.6s 0.7s;
        transition: transform 0.6s 0.7s;
        transition: transform 0.6s 0.7s, -webkit-transform 0.6s 0.7s;
    }

    .el__bg:before {
        content: "";
        position: absolute;
        left: 0;
        top: -5%;
        width: 100%;
        height: 110%;
        background-size: cover;
        background-position: center center;
        -webkit-transition: -webkit-transform 1s;
        transition: -webkit-transform 1s;
        transition: transform 1s;
        transition: transform 1s, -webkit-transform 1s;
        -webkit-transform: translate3d(0, 0, 0) scale(1);
        transform: translate3d(0, 0, 0) scale(1);
    }

