<template>
  <div id="app" style="margin-left:180px;margin-right:180px;" ondragstart="return false;">
    <v-header></v-header>
    <transition mode="out-in">
      <router-view v-on:showAlert="showAlert" v-on:getUser="getUser" :member="member" v-on:clearUser="clearUser" v-on:changeHireOrSale="changeHireOrSale">
      </router-view>
    </transition>
    <v-footer v-on:logout="logout" :member="member" :userName="userName" :hireOrSale="hireOrSale"></v-footer>

    <!-- Alert -->
    <div id="alertModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle"
      aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">通知</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{alertMsg}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import "bootstrap/dist/css/bootstrap.css";

  import vHeader from './components/VHeader.vue';
  import vFooter from './components/VFooter.vue';

  export default {
    name: 'app',
    components: {
      vHeader,
      vFooter
    },
    data() {
      return {
        alertMsg: "通知",
        member: {
          id: " ",
          tel: "",
          name: "",
          sex: "",
          age: "",
          level: ""
        },
        userName: "",
        hireOrSale:""
      }
    },
    methods: {
      showAlert(ifShow, alertMsg) {
        if (ifShow) {
          $('#alertModal').modal('show');
          this.alertMsg = alertMsg;
        } else {
          $('#alertModal').modal('hide');
          this.alertMsg = alertMsg;
        }
      },
      logout() {
        var confi = confirm("是否注销用户?");
        if (confi == true) {
          this.axios({
            method: "get",
            url: "/api/user/logout.action"
          }).then((response) => {
            var map = response.data;
            if (map.code == 200) {
              if (map.data == null) { //注销成功
                this.clearUser();
                this.$router.push("/home");
                this.showAlert(true, "注销成功");
              } else {
                this.showAlert(true, map.data);
              }
            }
          }).catch((error) => {
            console.log(error);
            this.showAlert(true, "注销失败,服务器异常");
          });
        }
      },
      getUser() {
        this.axios({
          method: "get",
          url: "/api/user/getUser.action"
        }).then((response) => {
          var map = response.data;
          if (map.code == 200) {
            if (map.data != null) { //查询已登录用户信息
              this.member = JSON.parse(map.data);
              this.userName = this.member.name;
            } else { //服务器可能出现错误
              this.clearUser();
            }
          } else if (map.code == 401) { //未登录
            this.clearUser();
          }
        }).catch((error) => {
          console.log(error);
          this.clearUser();
        });
      },
      clearUser() {
        this.member.id = "";
        this.member.tel = "";
        this.member.name = "";
        this.member.sex = "男";
        this.member.age = "";
        this.member.level = "";
        this.userName = "";
      },
      changeHireOrSale(val) {
        this.hireOrSale = val;
        console.log(this.hireOrSale);
      }
    },
    created() {
      this.clearUser();
      this.getUser();
    }
  }
</script>

<style>
  body {
    background-image: url('./assets/img/common/background_02.png');
    height: 100%;
    width: 100%;
    background-size: cover;
  }

  /* v-enter [这是一个时间点] 进入之前,元素初始状态,此时还没有开始进入 */
  /* v-leave-to [这是一个时间点] 是动画离开之后,离开的终止状态,此时动画已经结束 */
  .v-enter,
  .v-leave-to {
    opacity: 0;
    transform: translateX(1400px);
  }

  /* v-enter-active [入场动画的时间段] */
  /* v-leave-active [离场动画的时间段] */
  .v-enter-active,
  .v-leave-active {
    transition: all 0.4s ease;
  }

  .div_middle {
    text-align: center;
  }

  #main,
  main,
  .main {
    text-align: center;
    background-color: white;
    height: 710px;
    overflow-y: auto;
    overflow-x: hidden;
    opacity: 0.9;
  }
</style>