<template>
    <main id="login" style="padding-top:150px;">
        <div class="row">
            <div style="float:left;" class="col-4"></div>
            <div style="float:left;margin-left:15px;margin-right:15px;" class="col-4">
                <img src="../assets/img/common/title_login.png" style="height:180px;">
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">手机号码</span>
                    </div>
                    <input type="text" class="form-control" placeholder="Username" aria-label="Username"
                        aria-describedby="basic-addon1" v-model="member.tel"
                        onkeydown="if(event.keyCode==32){return false;}">
                </div>
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">登录密码</span>
                    </div>
                    <input type="password" class="form-control" placeholder="Password" aria-label="Password"
                        aria-describedby="basic-addon1" v-model="member.password"
                        onkeydown="if(event.keyCode==32){return false;}">
                </div>

                <a type="button" class="btn btn-primary" style="width:100%" @click="doLogin">登录</a><br>
                <a type="button" class="btn btn-light" style="width:100%" href="/#/register">注册新会员</a>
            </div>

            <div style="float:left;" class="col-4"></div>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'login',
        components: {

        },
        data() {
            return {
                member: {
                    tel: '',
                    password: ''
                }
            }
        },
        methods: {
            doLogin() {
                var tel = this.member.tel;
                var password = this.member.password;
                if (tel == "") {
                    this.$emit("showAlert", true, "请输入手机号码");
                } else if (!(/^0?1[3|4|5|6|7|8][0-9]\d{8}$/.test(tel))) {
                    this.$emit("showAlert", true, "请输入正确的手机号码");
                } else if (password == "") {
                    this.$emit("showAlert", true, "请输入密码");
                } else {
                    this.axios({
                        method: "post",
                        url: "/api/user/doLogin.action",
                        data: this.member
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) { //登录成功
                                this.$emit("getUser");
                                this.$router.push("/yachtHire");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        this.$emit("showAlert", true, "服务器未响应,请稍后重试");
                    });
                }
            }
        },
        created() {
            $(".modal-backdrop").remove();
        }
    }
</script>

<style scoped>
    #title {
        margin-top: 15px;
    }
</style>